.app {
  display: grid;
  grid-template-rows: 20% 80%;
  min-height: 99vh;
  max-height: 100vh;
  max-width: 100vw;
  font-family: Arial, Helvetica, sans-serif;
}

.canvas {
  grid-column-start: 1;
  grid-row-start: 1;
  grid-row-end: 3;
  background-color: lightgray;
  z-index: -1;
  animation: welcome 0.5s; 
  justify-self: stretch;
  align-self: stretch;
  overflow-x: scroll;
}

.item-container{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
  justify-content: space-around;
  -webkit-overflow-scrolling: touch;
}

.item {
  border: 1px solid black;
  background: white;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.5);
  border-radius: 15px;
  border-width: 2px;
  padding: 20px;
  animation: welcome 0.5s;
  margin: 20px;
  display: flex;
  max-height: 50%;
}

.item a {
  flex: 1;
  display: flex;
  min-height: 250px;
}

img {
  max-width: 250px;
  max-height: 250px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.5);
  align-self: center;
}

img:hover {
  box-shadow: 3px 3px 15px 1px rgba(0,0,0,0.7);
  transition-duration: 0.5s;
}

.welcome-container {
  grid-column: 1;
  grid-row-start: 1;
  display: flex;
  margin: 0px;
  justify-content: center;
}

.welcome {  
  font-size: 3rem;
  align-self: center;
  color: whitesmoke;
  text-shadow: 3px 3px 3px black;  
  animation: welcome 0.5s;
  margin: 0px;
  background: rgba(0,0,0,0.2);
  padding: 10px;
}

.intro {
  margin: 0px;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  align-self: center;
  justify-self: center;
  
  color: black;
  text-shadow: 1px 1px 1px black;
  opacity: 0;
  animation: intro 4s;
}

.itema{
  animation: items 1.5s;
}

.itemb{
  animation: items 2s;
}
.itemc{
  animation: items 2.5s;
}
.itemd{
  animation: items 3s;
}

@keyframes welcome {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes items {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0
  }
  100% {
    opacity: 1
  }
}


@keyframes intro {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}